.App {
  font-family: sans-serif;
  text-align: center;
}

.display-data{
  margin-top:50px;
  background-color: #adadcf;
  border-style: solid;
  border-radius: 20px;
  /* width: 900px; */
  margin: 20px;
  display:inline-block;
  justify-content: center;
  align-content: center;
}

.data{
  display:flex;
  flex-direction: row;
  justify-content: center;
  border-style: solid;
  width: 666px;
  margin: 20px;
  border-radius: 20px;
  border-width: 0;
  box-shadow: 3px 3px 8px whitesmoke;
  background-color: lightsalmon;
}

body{
  background:linear-gradient(90deg,#cf7080,#7dc27d,#d3d33c)
}

input{
  width: 300px;
  height: 30px;
  border-radius: 10px;
border-color: black;
text-align: center;
margin-left: 20px;
}
button{
  border-radius: 10px;
  width: 110px;
  height: 35px;
background-color: lightgreen;
font-weight: bold;
border-color: black;
margin-left: 100px;
}
.rating{
  margin-left: 54px;
}
label{
  font-weight: bold;
}